      console.log(e.currentTarget.dataset.oid);
      console.log('sdfgsdthtsrh-------' + e.currentTarget.dataset.cid);

      uni.request({
        url: getApp().globalData.url + '/order/updateOrderByOid?oId=' + e.currentTarget.dataset.oid,
