        header: {
          'content-type': 'application/json'
        },
        method: 'POST',
        dataType: 'json',
        success: function (res) {
          console.log('返回:' + res.data);

          if (res.data > 0) {
            uni.showToast({
              title: '成功',
              icon: 'succes',
              duration: 100,
              mask: true
            });
            uni.navigateTo({
              url: '/pages/payment/payment'
            });
          }
        }
