    },
    
	tuiku: function (e) {
      var that = this;
      var soid = e.currentTarget.id;
      console.log('取消 ' + soid);
      uni.showModal({
        title: '确定要取消支付吗',
        success: function (res) {
          if (res.confirm) {
            //这里是点击了确定以后
            uni.request({
              url: "http://localhost:8080/OrderFormApi/cancel?id="+soid,
              data: {},
              header: {
                'content-type': 'application/json'
              },
              method: 'GET',
              dataType: 'json',
              success: function (res) {
				  console.log(res);
				  console.log(res.data);
				  console.log(res.data.data);
				if(res.data.data>0){
					uni.showToast({
					  title: '取消订单成功',
					  icon: 'success',
					  duration: 500
					});
				}else{
					uni.showToast({
					  title: '操作太快',
					  icon: 'warning',
					  duration: 500
					});
				}
				// this.$set(this.ordersFor,res.data.data)
				that.load();
              }
            });
